<template>
  <div class="boxing">
    <h4>保单详细信息</h4>
    <el-form ref="form" :model="form" label-width="150px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="保单编号">
            <el-input v-model="form.policyNum"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="订单编号">
            <el-input v-model="form.itemOrder.orderNum"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="投保项目">
            <el-input v-model="form.item.detail"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="投保项目编号">
            <el-input v-model="form.item.itemNum"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="投保客户">
            <el-input v-model="form.member.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="投保客户身份证号">
            <el-input v-model="form.member.identify"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="授权书">
            <el-input v-model="form.warrant"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item>
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="cancel">取消</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { constants } from "fs";
export default {
  data() {
    return {
      form: {
        policyNum: "",
        member: "",
        item: "",
        itemOrder: "",
        warrant: "",
      }
    };
  },
  created() {
    var that = this;
    if (this.$route.query.id) {
      const id = this.$route.query.id;
      that.$axios
        .get("/policyorders/" + id, 
        // {
        //   headers: { Authorization: sessionStorage.getItem("auth") }
        // }
        )
        .then(function(res) {
            console.log(res);
          that.form = res.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    } 
  },
  methods: {
    save() {
      var that = this;
      var id = that.form.id;
      var policyNum = that.form.policyNum;
      var member = that.form.member;
      var item = that.form.item;
      var itemOrder = that.form.itemOrder;
      var warrant = that.form.warrant;
      if (this.$route.query.id) {
        console.log("1")
        // 修改
        this.$axios({
          method: "put",
          url: "/policyorders",
          // headers: { Authorization: sessionStorage.getItem("auth") },
          data: {
              id: id,
              policyNum: policyNum,
              member: member,
              item: item,
              itemOrder: itemOrder,
              warrant: warrant
          }
        })
          .then(function(res) {
            console.log(res);
            if (res.status == 200) {
              that.$message({
                message: "修改成功",
                type: "success"
              });
              that.$router.push("/policyorder/list");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        console.log("2")
        // 添加
        this.$axios
          .post(
            "/policyorders",
            {
              mid: mid,
              name: name,
              mobile: mobile,
              identify: identify,
              sex: sex,
              headImage: headImage
            },
            {
              headers: {
                // Authorization: sessionStorage.getItem("auth")
              }
            }
          )
          .then(function(res) {
            console.log(res);
            if (res.status == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.$router.push("/policyorder/list");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },

    cancel(){
      this.$router.push("/policyorder/list");
    }
  }
};
</script>


<style>
.boxing {
  padding: 0 50px;
  height: 500px;
  /* background: red; */
  margin: 0 auto;
}
h4 {
  text-align: center;
  line-height: 80px;
}
</style>



